                    <div>
                        <router-view />
                    </div>
                    <ul class="ui-autocomplete ui-front ui-menu ui-widget ui-widget-content" id="ui-id-1" tabindex="0" style="display: none;"></ul>
                    <ul class="ui-autocomplete ui-front ui-menu ui-widget ui-widget-content" id="ui-id-3" tabindex="0" style="display: none;"></ul>
                </section>

                <!-- PAGE FOOTER -->
                <footer class="ftr pag">
                    <div class="disclosures">
                        Mafia track records |
                    </div>
                </footer>
            </div>
        </div>
    </div>
</template>

<script>
    import {HTTP, Routes} from '@/components/Routes/Routes'
    import TopMenu from "@/components/Main/TopMenu";
    export default {
        name: 'app',
        components: {
            TopMenu
        }
    }
</script>

<style>
/*header*/
.ui-panel-wrapper {
	position: relative;
	min-height: inherit;
	border: 0;
	overflow-x: hidden;
	z-index: 30 !important;
}
.pag {
	background-color: #ecf0f2;
	height: 100%;
}

/*body*/
.pag.bdy {
    width: 100%;
	overflow: hidden;
	margin-top: 0;
	min-height: 730px;
}
.pag.bdy > div, .pag.ftr > div {
	max-width: 95%;
	margin: 0 auto;
}
.pag.bdy .bdy.content.reset {
    padding: 0;
    background-color: transparent;
    border: 0;
}
.pag.bdy .bdy.content {
    overflow: hidden;
    border: 1px solid #ddd;
    margin-bottom: 20px;
}
.blk.__content-mount {
    background-color: white;
    border: 1px solid #ddd;
    margin-bottom: 25px;
}
.blk {
    display: block;
    overflow: hidden;
}
.blk.__content-mount > .title {
    padding: 30px 25px 25px;
    border-bottom: 1px solid #ddd;
}
.blk.__content-mount > h2, .blk.__content-mount > h3, .blk.__content-mount > h4, .blk.__content-mount > h5 {
    margin: 20px auto 15px;
}
.blk.__content-mount > h2 {
