}
.blk.__content-mount > h3 {
    font-size: 2em;
}
.blk.__content-mount > p, .blk.__content-mount > ol > li, .blk.__content-mount > ul > li {
    margin-bottom: 50px;
	line-height: 1.7em;
	opacity: .6;
	font-size: 1.4em;
	padding-bottom: 10px;
}
.blk.__content-mount > p, .blk.__content-mount > ol, .blk.__content-mount > ul {
	color: #555;
}
.blk.__content-mount > p, .blk.__content-mount > h2, .blk.__content-mount > h3, .blk.__content-mount > h4, .blk.__content-mount > h5 {
    padding: 0 25px;
    line-height: 1.6em;
}
.blk.__content-mount > p {
    font-size: 1.8em;
    font-family: Georgia,"Times New Roman",Times,serif;
}

/*footer*/
.pag.ftr {
	padding: 35px 15px;
	background-color: #374044 !important;
}
.pag.ftr .disclosures {
	font-style: italic;
	color: #fff;
	opacity: .5;
}
.pag.bdy > div, .pag.ftr > div {
	max-width: 95%;
	margin: 0 auto;
}
</style>



