<template>
    <header class="pag hdr ">
        <div>
            <nav class="nav primary "> 
                <ul class="lst menu">
                    <li class="logo-itm">
                        <a v-on:click="setActive('')" class="logo" @click="switchToMain">Mafia track records</a>
                    </li>
                    <li class="smenu">
                        <a v-on:click="setActive('players')" :class="{ on:isActive('players') }" class="pnl-button" @click="switchToPlayers">
                            <span>Игроки</span>
                        </a>
                    </li>
                    <li class="smenu">
                        <a data-supermenu="#mlb-supermenu" href="https://rotogrinders.com/daily-fantasy-baseball" class="pnl-button">
                            <span class="icn icn-baseball"></span>
                            <span>MLB</span>
                        </a>
                    </li>
                    <li class="smenu">
                        <a data-supermenu="#pga-supermenu" href="https://rotogrinders.com/daily-fantasy-golf" class="pnl-button">
                            <span class="icn icn-golf"></span>
                            <span>PGA</span>
                        </a>
                    </li>
                    <li class="single-item">
                        <a href="https://rotogrinders.com/lineuphq">
                            <span class="icn icn-tools"></span>
                            <span>LineupHQ</span>
                        </a>
                    </li>
                    <li class="single-item">
                        <a href="https://rotogrinders.com/lineups">
                            <span class="icn icn-megaphone"></span>
                            <span class="title">Starting Lineups</span>
                        </a>
                    </li>
                    <li class="single-item">
                        <a href="https://rotogrinders.com/threads/category/main">
                            <span class="icn icn-conversation"></span>
                            <span>Forum</span>
                        </a>
                    </li>
                    <li class="single-item">
                        <a href="https://rotogrinders.com/videos">
                            <span class="icn icn-play2"></span>
                            <span>Video</span>
                            <span class="onair hidden" style="display: inline;">On Air</span>
                        </a>
                    </li>
                    <li class="single-item">
                        <a href="https://rotogrinders.com/fanduel-single-entry-series/mlb">
                            <span class="icn icn-trophy"></span>
                            <span class="title">FD SES</span>
                        </a>
                    </li>
                    <li class="single-item">
                        <a class="discord" href="https://discordapp.com/invite/GqgXZCk" target="_blank">
                            <span>Discord</span>
                            <span class="onair">New</span>
                        </a>
                    </li>
                    <li class="more-menu" style="display: none;">
                        <a href="" id="prime-nav-more-btn">More<span class="icn-arw-down"></span></a>
                        <ul class="lst" id="prime-more-menu" style="display: none;"></ul>
                    </li>
                </ul>
            </nav>
        </div>
        <div class="main__header-fixer"></div>
    </header>
</template>

<script>
    import {HTTP, Routes} from '@/components/Routes/Routes'

    export default {
        data() {
            return { activeItem: '' }
        },
        methods: {
            isActive: function (menuItem) {
                return this.activeItem === menuItem
            },
            setActive: function (menuItem) {
                this.activeItem = menuItem
            },
            switchToMain () {
                this.$router.push({name: 'Main'})
            },
            switchToPlayers () {
                this.$router.push({name: 'Players'})
            },
        }
    }
</script>

<style>
    .pag.hdr {
        overflow: hidden;
        width: 100%;
        height: auto;
    }
    .pag.hdr > div {
        position: fixed;
        z-index: 40;
        width: 100%;
    }
    .pag.hdr .nav {
        width: 100%;
    }
    .nav.primary {
        background-color: #374044;
        box-shadow: 0 0 7px rgba(0,0,0,0.7);
        cursor: pointer;
    }
    .nav.primary > .lst.menu {
        font-size: 1.15em;
        height: 70px;
        display: block;
        width: 100%;
        position: relative;
        overflow: hidden;
        margin: 0;
    }
    .nav.primary > .lst.menu > .logo-itm {
        width: auto;
        margin: 0;
        display: block;
    }
    .nav.primary > .lst.menu > li {
        display: block;
        float: left;
        border-right: 1px solid rgba(255,255,255,0.07);
    }
    .nav.primary > .lst.menu > .logo-itm > a {
        display: block;
        margin: 0 auto;
        height: 70px;
        line-height: 70px;
        text-align: center;
    }
    .nav.primary > .lst > li > a:hover, .nav.secondary .lst > li > a:hover {
        background-color: rgba(255,255,255,0.04);
        text-decoration: none;
    }
    .nav.primary > .lst > li > a, .nav.secondary .lst > li > a {
        color: #fff;
    }
    .nav.primary > .lst.menu .logo {
        width: 250px;
        background-position: 50% -3px;
        text-indent: -9999px;
        padding: 0 15px;
        background: url("/static/images/mtr.png") no-repeat 50% 0/95% transparent;
    }
    .nav.primary > .lst.menu > li > a {
        height: 70px;
        line-height: 70px;
        padding: 0 15px;
        display: block;
        text-align: center;
        color: #fff;
    }
    .nav.primary > .lst.menu > li > .on, .nav.primary > .lst > li > .on:hover {
        background-color: #f98c13;
        color: #fff;
    }
    .pag.hdr > .main__header-fixer {
        margin-top: 70px;
        display: block;
        position: relative;
        background-color: #2d363b;
        font-size: 1em;
        z-index: 30;
    }
</style>


